import React, { Component } from "react";
import { Col, Table, Button } from "react-bootstrap";
import Card from "components/Card/Card.jsx";
import TanksTable from "../../app/db/TanksTable"
import { getFormattedTime } from "app/adminHelper.jsx"
import { makePdf } from "./PdfMaker"
import { getPrimaryTextColor, isMobile } from '../../app/adminHelper'

class TableList extends Component {

  constructor(props) {
    super(props);
    this.arrayForTable = [];
    this.arrayForExcel = [[]];
    this.arrayForPdf = [[]];
    this.tanksNames = [];
    this.state = { excelTableReady: false };
  }

  componentDidMount = () => {
    import('./ExcelTable').then(ExcelTable => {
      this.ExcelTable = ExcelTable;
      this.setState({ excelTableReady: true });
      console.log("excelTable Ready");
    })
  }

  getHeader = () => {
    return ["Tank", "Left in Tank (%)", "Volume", "Time"];
  }

  createData = () => {
    this.arrayForExcel = [[]];
    this.arrayForPdf = [[]];
    this.arrayForTable = [];
    this.tanksNames = [];
    let tt = new TanksTable();
    for (let index in this.props.rangeReadings) {
      let tank = tt.getTank(this.props.rangeReadings[index].imei);
      let tankName = tank.name;
      this.tanksNames.push(tankName);
      this.arrayForExcel[index] = [];
      this.arrayForPdf[index] = [];
      this.arrayForPdf[index].push([tankName, 'Time', 'Left In Tank', 'Volume']);
      for (let readings in this.props.rangeReadings[index].levels) {
        let time = getFormattedTime(this.props.rangeReadings[index].times[readings]);
        let level = this.props.rangeReadings[index].levels[readings];
        let sample = this.props.rangeReadings[index].samples[readings];
        let vol = parseInt(level * tank.capacity / 100000, 10);
        let row = [
          tankName,
          level + " %",
          vol + "K L",
          time];
        this.arrayForTable.push(row);
        this.arrayForExcel[index].push({ time: time, level: level + " %", vol: vol + "K L", samples: sample });
        this.arrayForPdf[index].push(['', time, level + " %", vol + "K L"]);
      }
      if (this.props.rangeReadings[index].levels.length === 0) {
        // tank without readings history
        let row = [
          tankName,
          "No History For This Tank",
          "!!!!!!!",
          "!!!!!!!"];
        this.arrayForTable.push(row);
      }
    }
  }

  creatPdfDocDefinition = () => {
    let array = [];
    for (let index in this.arrayForPdf) {
      let pb = { text: '', pageBreak: 'after', style: 'subheader' };

      let obj = {
        style: 'tableExample',
        table: {
          headerRows: 1,
          widths: ['*', '*', '*', '*'],
          body: this.arrayForPdf[index]
        }
      };
      array.push(obj);
      array.push(pb);
    }

    return array;
  }

  exportToPdf = (exportOrPrint) => {
    makePdf(exportOrPrint, this.creatPdfDocDefinition());
  }

  renderExport = () => {
    console.log("renderExport");
    return (
      <div>
        <div>
          {this.props.rangeButtons}
        </div>
        {isMobile() === false &&
          <div>
            <this.ExcelTable.default button={<Button bsStyle="primary" style={{ margin: '3px', color: getPrimaryTextColor(), borderColor: getPrimaryTextColor() }} >Export To Excel</Button>} data={this.arrayForExcel} tanksNames={this.tanksNames} />
            <Button bsStyle="primary" style={{ margin: '3px', color: getPrimaryTextColor(), borderColor: getPrimaryTextColor() }} onClick={() => this.exportToPdf('export')}>Export To PDF</Button>
            {isMobile() === false &&
              <Button bsStyle="primary" style={{ margin: '3px', color: getPrimaryTextColor(), borderColor: getPrimaryTextColor() }} onClick={() => this.exportToPdf('print')}>Print</Button>
            }
          </div>
        }
      </div>
    );
  }

  render() {

    if (this.state.excelTableReady === false) {
      return null;
    }
    console.log("render TableList ");

    this.createData();
    let header = this.getHeader();
    return (
      <Col md={12}>
        <Card
          title={this.props.title}
          category={this.props.category}
          hCenter={true}
          ctTableFullWidth
          stats={this.renderExport()}
          content={
            <div style={{ height: '50%', overflowY: 'auto' }}>
              <Table striped hover >
                <thead >
                  <tr>
                    {header.map((prop, key) => {
                      return <th key={key}>{prop}</th>;
                    })}
                  </tr>
                </thead>
                <tbody style={{ overflow: 'auto' }}>
                  {this.arrayForTable.map((prop, key) => {
                    return (
                      <tr key={key}>
                        {prop.map((prop, key) => {
                          return <td key={key}>{prop}</td>;
                        })}
                      </tr>
                    );
                  })}
                </tbody>
              </Table>
            </div>
          }
        />
      </Col>
    );
  }
}

export default TableList;
