import React, { Component } from "react";
import { Navbar } from "react-bootstrap";

import HeaderLinks from "./HeaderLinks.jsx";
import { getPrimaryColor, getPrimarySideColor } from "../../app/adminHelper"
import dashboardRoutes from "routes/dashboard.jsx";
import logo from "assets/img/orbitBanner3000.png";

class Header extends Component {
  constructor(props) {
    super(props);
    this.mobileSidebarToggle = this.mobileSidebarToggle.bind(this);
    this.state = {
      sidebarExists: false
    };
  }
  mobileSidebarToggle(e) {
    if (this.state.sidebarExists === false) {
      this.setState({
        sidebarExists: true
      });
    }
    e.preventDefault();
    document.documentElement.classList.toggle("nav-open");
    //    var node = document.createElement("div");
    //    node.id = "bodyClick";
    //    node.onclick = function() {
    //      this.parentElement.removeChild(this);
    //      document.documentElement.classList.toggle("nav-open");
    //    };
    //    document.body.appendChild(node);
  }
  getBrand() {
    var name;
    dashboardRoutes.map((prop, key) => {
      if (prop.collapse) {
        prop.views.map((prop, key) => {
          if (prop.path === this.props.location.pathname) {
            name = prop.name;
          }
          return null;
        });
      } else {
        if (prop.redirect) {
          if (prop.path === this.props.location.pathname) {
            name = prop.name;
          }
        } else {
          if (prop.path === this.props.location.pathname) {
            name = prop.name;
          }
        }
      }
      return null;
    });
    return name;
  }
  render() {
    let width = window.innerWidth;
    let headerHeigth = '11.2%';
    let showLogo = false;
    let imageHeigth = '80%';
    if (width < 991) {
      showLogo = true;
    }

    if (true) {
      return (
        <Navbar fluid style={{ backgroundColor: getPrimaryColor() }} >
          <Navbar.Toggle onClick={this.mobileSidebarToggle} />
          {showLogo === true &&
            <center style={{height: headerHeigth}}>
              <a href="#pablo">
                <img src={logo} alt="logo_image" style={{ height: imageHeigth, marginLeft:'10%', marginTop:'2%' }} />
              </a>
            </center>
          }
          {showLogo === false &&
            <center style={{height: headerHeigth}}>
              <a href="#pablo" style={{ color: getPrimarySideColor() }}><b>{this.getBrand()}</b></a>
            </center>
          }
        </Navbar>
      );
    }

    return (
      <Navbar fluid style={{ backgroundColor: getPrimaryColor() }} >
        <Navbar.Header style={{ textAlign: 'center', width: '100%', float: 'none', display: 'inlineBlock' }}>
          <Navbar.Brand>
            <div >&nbsp;&nbsp;&nbsp;&nbsp;</div>
          </Navbar.Brand>
          <Navbar.Brand>
            <a href="#pablo" style={{ color: getPrimarySideColor(), height: headerHeigth }}><b>{this.getBrand()}</b></a>
          </Navbar.Brand>
          <Navbar.Brand style={{ position: 'absolute', width: '100%', left: '0', textAlign: 'center', margin: '0 auto' }}>
            {showLogo === true && <img src={logo} alt="logo_image" style={{ height: '10%' }} />}
          </Navbar.Brand>
          <Navbar.Toggle onClick={this.mobileSidebarToggle} />
        </Navbar.Header>
        <Navbar.Collapse>
          <HeaderLinks />
        </Navbar.Collapse>
      </Navbar>
    );
  }
}

export default Header;
